import { Component, OnInit } from "@angular/core";
import { DocumentModel } from '../models/document.model';
import { DocumentsService } from '../services/documents.service';


@Component({
    selector: 'get-document',
    templateUrl: 'get-document.html',
    styleUrls: ['get-document.css'],
})
export class GetDocumentComponent implements OnInit {
    private documents : DocumentModel[];
    private fonts: string[];
    private fontSizes: number[];
    private lineSpacings: number[];

    private document: number;
    private font: string;
    private fontSize: number;
    private lineSpacing: number;

    constructor(private documentService: DocumentsService) {}

    ngOnInit() {
        this.documentService.getDocuments().subscribe(data => {
            if (data != null) {
                this.documents = data
                return
            }
            this.documents = [];
        })
        this.fonts = ["Arial", "Times New Roman", "Calibri"];
        this.fontSizes = [12, 14, 16, 18]
        this.lineSpacings = [1, 1.5, 2];
    }

    formDocument() {
        console.log(this.document)
        let req = {
            ID: this.document,
            Font: this.font,
            FontSize: this.fontSize,
            Interval: this.lineSpacing
        }
        this.documentService.formDocument(req).subscribe(data => {
            let object = JSON.parse(data) 
            console.log(object)
            window.open(object.url)
        })
    }


}